import DescriptionIcon from '@mui/icons-material/Description';
import PersonIcon from '@mui/icons-material/Person';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import { Box, CircularProgress, IconButton, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';
import { ObjectViewer, AddedProductsViewer } from '../../components/ObjectViewer';
import { useDocumentContext } from '../../state/documentContext';
import { WhitePaperContainer } from './styled-components';

const DocumentDetailsPage: React.FC = () => {
  const { t } = useTranslation();
  const { hash } = useParams<{ hash: string }>();
  const { getDocument } = useDocumentContext();
  const [document, setDocument] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [activeSection, setActiveSection] = useState('details');

  useEffect(() => {
    const fetchDocument = async () => {
      try {
        const doc = await getDocument(hash);
        setDocument(doc);
      } catch (err) {
        setError(t('Error loading document'));
        console.error(err);
      } finally {
        setLoading(false);
      }
    };
    fetchDocument();
  }, [hash, getDocument, t]);

  const handleTabChange = (section: string) => {
    setActiveSection(section);
  };

  const renderContent = () => {
    if (!document) return null;

    switch (activeSection) {
      case 'details':
        return <ObjectViewer data={document.data?.quoteHeadDetails} />;
      case 'client':
        return <ObjectViewer data={document.data?.selectedClient} />;
      case 'products':
        return <AddedProductsViewer products={document.data?.addedProducts} />;
      default:
        return <Typography>{t('No data available')}</Typography>;
    }
  };

  if (loading) {
    return (
      <WhitePaperContainer>
        <Box display="flex" justifyContent="center" alignItems="center" height="100vh">
          <CircularProgress />
        </Box>
      </WhitePaperContainer>
    );
  }

  if (error) {
    return (
      <WhitePaperContainer>
        <Typography color="error">{error}</Typography>
      </WhitePaperContainer>
    );
  }

  return (
    <WhitePaperContainer>
      {/* Tabs section */}
      <Box display="flex" alignItems="center" justifyContent="flex-start" p={2} overflow="auto">
        <IconButton
          onClick={() => handleTabChange('details')}
          sx={{ borderRadius: 2, p: 1, mr: 1, transition: 'background-color 0.3s', '&:hover': { backgroundColor: '#f0f0f0' } }}
        >
          <DescriptionIcon />
        </IconButton>

        <IconButton
          onClick={() => handleTabChange('client')}
          sx={{ borderRadius: 2, p: 1, mr: 1, transition: 'background-color 0.3s', '&:hover': { backgroundColor: '#f0f0f0' } }}
        >
          <PersonIcon />
          <Typography noWrap sx={{ ml: 1, overflow: 'hidden', textOverflow: 'ellipsis', maxWidth: '100px' }}>
            {t('Client Information')}
          </Typography>
        </IconButton>

        <IconButton
          onClick={() => handleTabChange('products')}
          sx={{ borderRadius: 2, p: 1, mr: 1, transition: 'background-color 0.3s', '&:hover': { backgroundColor: '#f0f0f0' } }}
        >
          <ShoppingCartIcon />
          <Typography noWrap sx={{ ml: 1, overflow: 'hidden', textOverflow: 'ellipsis', maxWidth: '100px' }}>
            {t('Products')}
          </Typography>
        </IconButton>
      </Box>

      {/* Main content section */}
      <Box flexGrow={1} p={3}>
        <Typography variant="h4" gutterBottom>{t('Document Details')}</Typography>
        {renderContent()}
      </Box>
    </WhitePaperContainer>
  );
};

export default DocumentDetailsPage;
