import React, { useState } from 'react';
import { Box, Typography, IconButton, Collapse, Avatar } from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import { formatPrice } from '../../utils/format-price';
import { dateText } from '../../utils/date-text'; // Import dateText
import { ProductType, ComponentType } from '../../types';
import { useTranslation } from 'react-i18next';

interface ObjectViewerProps {
  data: any;
  level?: number;
}

const ObjectViewer: React.FC<ObjectViewerProps> = ({ data, level = 0 }) => {
  const { t } = useTranslation()
  const [expanded, setExpanded] = useState<Record<string, boolean>>({});

  const toggleExpand = (key: string) => {
    setExpanded(prev => ({ ...prev, [key]: !prev[key] }));
  };

  const renderProduct = (product: ProductType, index: number) => {
    const { name, price, components = [], discount = 0, createdAt, updatedAt } = product;
    const discountedPrice = price - (price * discount) / 100;

    return (
      <Box key={index} sx={{ mb: 2 }}>
        <Typography variant="h6" fontWeight="bold" onClick={() => toggleExpand(name || `Product ${index + 1}`)}>
          {name || `Product ${index + 1}`} {expanded[name || `Product ${index + 1}`] ? <ExpandLessIcon /> : <ExpandMoreIcon />}
        </Typography>
        <Collapse in={expanded[name || `Product ${index + 1}`]}>
          <Box ml={2}>
            <Typography variant="body2">
              {`Price: ${formatPrice(price)} `}
              {discount > 0 && (
                <span style={{ textDecoration: 'line-through', marginRight: '8px' }}>
                  {formatPrice(discountedPrice)}
                </span>
              )}
            </Typography>
            <Typography variant="body2">{`Category: ${product.category}`}</Typography>
            <Typography variant="body2">{`Description: ${product.description || 'No description'}`}</Typography>
            <Typography variant="body2">{`Company: ${product.company || 'Unknown'}`}</Typography>

            {/* Using dateText for createdAt and updatedAt */}
            <Typography variant="body2">{`Created At: ${createdAt ? dateText(createdAt) : 'N/A'}`}</Typography>
            <Typography variant="body2">{`Updated At: ${updatedAt ? dateText(updatedAt) : 'N/A'}`}</Typography>

            {/* Handle Components */}
            {components.length > 0 && (
              <Box mt={2}>
                <Typography variant="subtitle2" fontWeight="bold">
                  {t('Components')}
                </Typography>
                {components.filter((comp: ComponentType) => comp.included).map((component, compIndex) =>
                  renderComponent(component, compIndex)
                )}
              </Box>
            )}
          </Box>
        </Collapse>
      </Box>
    );
  };

  const renderComponent = (component: ComponentType, index: number) => {
    const { name, price, quantity } = component;
    return (
      <Box key={index} sx={{ ml: 2, mb: 1 }}>
        <Typography variant="body2" fontWeight="bold">
          {name || `Component ${index + 1}`}
        </Typography>
        <Typography variant="body2">{`Price: ${formatPrice(price)}`}</Typography>
        <Typography variant="body2">{`Quantity: ${quantity}`}</Typography>
      </Box>
    );
  };

  const renderArray = (value: any[]) => {
    return (
      <Box>
        {value.map((item, index) => {
          if (item.name) {
            // Assume the top level is a product array
            return renderProduct(item, index);
          }
          return <ObjectViewer data={item} level={level + 1} key={index} />;
        })}
      </Box>
    );
  };

  const renderValue = (key: string, value: any): JSX.Element => {
    if (value === null || value === undefined) {
      return <Typography variant="body2" color="text.secondary">Not set</Typography>;
    }

    if (typeof value === 'boolean') {
      return <Typography variant="body2">{value ? 'Yes' : 'No'}</Typography>;
    }

    if (typeof value === 'number') {
      return <Typography variant="body2">{formatPrice(value)}</Typography>;
    }

    if (typeof value === 'string') {
      return <Typography variant="body2">{value}</Typography>;
    }

    if (Array.isArray(value)) {
      return renderArray(value); // Render products/components
    }

    if (typeof value === 'object') {
      return (
        <>
          <IconButton size="small" onClick={() => toggleExpand(key)}>
            {expanded[key] ? <ExpandLessIcon /> : <ExpandMoreIcon />}
          </IconButton>
          <Collapse in={expanded[key]}>
            <Box ml={2}>
              <ObjectViewer data={value} level={level + 1} />
            </Box>
          </Collapse>
        </>
      );
    }

    return <Typography variant="body2">{String(value)}</Typography>;
  };

  if (!data || Object.keys(data).length === 0) {
    return <Typography variant="body2" color="text.secondary">{t('NoResult')}</Typography>;
  }

  return (
    <Box sx={{ mb: 2 }}>
      {Object.entries(data).map(([key, value], index) => (
        <Box key={key} sx={{ display: 'flex', borderBottom: index < Object.entries(data).length - 1 ? '1px solid #e0e0e0' : 'none', py: 1 }}>
          <Box sx={{ flexBasis: '30%', pr: 2 }}>
            <Typography variant="subtitle2" fontWeight="medium">{key}</Typography>
          </Box>
          <Box sx={{ flexBasis: '70%' }}>
            {renderValue(key, value)}
          </Box>
        </Box>
      ))}
    </Box>
  );
};

interface AddedProductsViewerProps {
  products: ProductType[];
}

const AddedProductsViewer: React.FC<AddedProductsViewerProps> = ({ products }) => {
  const { t } = useTranslation();
  const renderComponent = (component: ComponentType, index: number) => {
    const { name, price, quantity, included } = component;

    // Skip components that aren't included
    if (!included) return null;

    return (
      <Box key={index} sx={{ ml: 3, mb: 1 }}>
        <Typography variant="body2" fontWeight="bold">{name} (x{quantity})</Typography>
        <Typography variant="body2">{`Price: ${formatPrice(price)}`}</Typography>
      </Box>
    );
  };

  const renderProduct = (product: ProductType, index: number) => {
    const { name, price, discount = 0, category, description, company, components = [], createdAt, updatedAt } = product;
    const discountedPrice = price - (price * discount) / 100;

    return (
      <Box key={index} sx={{ mb: 3, borderBottom: '1px solid #e0e0e0', pb: 2 }}>
        <Typography variant="h6" fontWeight="bold">{name}</Typography>

        {/* Product Details */}
        <Typography variant="body2">{`Price: ${formatPrice(price)}`}</Typography>
        {discount > 0 && (
          <Typography variant="body2" color="text.secondary">
            {`Discounted Price: ${formatPrice(discountedPrice)}`}
          </Typography>
        )}
        <Typography variant="body2">{`Category: ${category}`}</Typography>
        <Typography variant="body2">{`Description: ${description || t('NoDescriptionAvailable')}`}</Typography>
        <Typography variant="body2">{`Company: ${company || 'Unknown'}`}</Typography>

        {/* Dates */}
        <Typography variant="body2">{`Created At: ${dateText(createdAt as string)}`}</Typography>
        <Typography variant="body2">{`Updated At: ${dateText(updatedAt as string)}`}</Typography>

        {/* Components */}
        {components.length > 0 && (
          <Box mt={2}>
            <Typography variant="subtitle2" fontWeight="bold">
              {t('Components')}:
            </Typography>
            {components.map((component, compIndex) => renderComponent(component, compIndex))}
          </Box>
        )}
      </Box>
    );
  };

  return (
    <Box sx={{ mt: 2 }}>
      {products.map((product, index) => renderProduct(product, index))}
    </Box>
  );
};

export { AddedProductsViewer, ObjectViewer };

